@import url(https://fonts.googleapis.com/css2?family=Kalam:wght@300;400&display=swap);
.game_menu.card_container_container {
    padding: 0 1rem 0 1rem;
}

.game_menu.card_container {
    position: relative;
    max-width: 40rem;
    margin: 9.5rem auto 0 auto;
    text-align: center;
    padding-top: 2rem;
    border-radius: 3.5rem;
    box-shadow: 1px 1px 2px 1px rgba(20, 20, 20, 0.3);
}

.game_menu.button {
    margin: 1.5rem auto;
    width: 82%;
    padding: 2rem;
    font-size: 3rem;
    font-family: "Kalam", cursive;
    border-width: 0.3rem;
    border-radius: 5rem;
    box-shadow: 2px 2px 4px 3px rgba(10, 10, 10, 0.3);
    text-transform: uppercase;
    transition: all 0.2s ease-in-out;
    position: relative;
    z-index: 2;
}

.game_menu.button:hover {
    border-width: 0.3rem;
}

.help_container {
    text-align: left;
    margin: 0 5rem;
}

.slide_down {
    -webkit-animation: slideDown 0.3s forwards cubic-bezier(0.2, 1, 0.3, 1);
            animation: slideDown 0.3s forwards cubic-bezier(0.2, 1, 0.3, 1);
    z-index: 1;
}

.slide_up {
    -webkit-animation: slideUp 0.2s forwards cubic-bezier(0.2, 1, 0.3, 1);
            animation: slideUp 0.2s forwards cubic-bezier(0.2, 1, 0.3, 1);
    z-index: 1;
}

@-webkit-keyframes slideDown {
    0% {
        opacity: 0;
        visibility: hidden;
        -webkit-transform: translateY(-15rem);
                transform: translateY(-15rem);
        height: 0;
    }

    100% {
        opacity: 1;
        visibility: visible;
        display: unset;
        height: unset;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slideDown {
    0% {
        opacity: 0;
        visibility: hidden;
        -webkit-transform: translateY(-15rem);
                transform: translateY(-15rem);
        height: 0;
    }

    100% {
        opacity: 1;
        visibility: visible;
        display: unset;
        height: unset;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes slideUp {
    0% {
        opacity: 1;
        visibility: visible;
        height: unset;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    100% {
        opacity: 0;
        visibility: hidden;
        height: 0;
        -webkit-transform: translateY(-15rem);
                transform: translateY(-15rem);
    }
}

@keyframes slideUp {
    0% {
        opacity: 1;
        visibility: visible;
        height: unset;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    100% {
        opacity: 0;
        visibility: hidden;
        height: 0;
        -webkit-transform: translateY(-15rem);
                transform: translateY(-15rem);
    }
}

@media (max-width: 650px) {
    .game_menu.card_container {
        margin-top: 8rem;
    }

    .game_menu.button {
        padding: 1rem;
        width: 26rem;
    }
}

@media (max-width: 550px) {
    .game_menu.card_container {
        margin-top: 7.5rem;
    }
    .game_menu.button {
        font-size: 2.5rem;
        width: 22rem;
    }
}

@media (max-width: 440px) {
    .game_menu.card_container {
        margin-top: 7rem;
    }
    .game_menu.button {
        font-size: 2.2rem;
        width: 18rem;
    }
}

@media (max-width: 380px) {
    .game_menu.button {
        font-size: 1.8rem;
        width: 16rem;
    }
}


.options_subsection .subheading {
    color: var(--text-purple-dark);
    font-weight: 500;
    margin-bottom: 1rem;
}

.options_container [type=radio] {
    transition: all ease-in-out 0.2s;
    visibility: hidden;
    width: 0;
    height: 0;
}

.subsection.options_subsection {
    background: white;
    padding-bottom: 3rem;
    border-radius: 1rem;
    margin-bottom: 2rem;
    margin-top: 0;
}

.option_container {
    transition: all ease-in-out 0.2s;
    display: inline-block;
    width: 20rem;
}

.option_container .option:hover {
    background: var(--yellow-green);
}

.option_container .option:active {
    background: radial-gradient(circle, var(--yellow-green), var(--yellow));
}

.option_container .option {
    transition: all ease-in-out 0.2s;
    color: var(--text-purple-dark);
    background-color: var(--yellow);
}

[type=radio]:checked + .option {
    transition: all ease-in-out 0.2s;
    background: var(--main-green);
    color: white;
    box-shadow: 1px 1px 4px 3px rgba(10, 100, 30, 0.5);
}

@media (max-width: 380px) {
    .option_container {
        width: 18rem;
    }
}

